import { Component, OnDestroy, OnInit, ViewChild } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { Library } from '@core/library';
import { AuthService, SocketService } from '@core/services';
import { SearchbillService } from '@modules/search-bills/shared/searchbill.service';
import { VatService } from '@modules/vat/shared';
import { TranslateService } from '@ngx-translate/core';
import { SignatureOffComponent } from '@shared/index';
import { SendDataForTaxComponent, ViewInvoiceMultipageComponent } from '@shared/components';
import { BsModalRef, BsModalService } from 'ngx-bootstrap';
import { NgxSpinnerService } from 'ngx-spinner';
import { Subscription } from 'rxjs';
import { Helper, VChanggeMoneyService, VPreviewInvService, VStringService, sEnum } from '@core/index';
import { BillService } from '@modules/bill-invoice/shared/bill.service';
import { VSelectBoxService } from '@core/library/public/v-select-box.service';
import { ViewInvoiceService } from '@shared/service/view-invoice.service';
import { InvoiceService } from '@shared/service/invoice.service';
import { DxDataGridComponent } from 'devextreme-angular';

@Component({
  selector: 'app-index',
  templateUrl: './index.component.html',
  styleUrls: ['./index.component.scss']
})
export class IndexComponent implements OnInit {
  @ViewChild(DxDataGridComponent) dataGrid: DxDataGridComponent;
  trangThaiGuiCQTStatus: any = [];
  dataSource: any;
  selectedRows: any;
  pageSelect: any;
  _translate: any;
  _language: any;
  configs: any;
  clickCurent = '';
  isTT78: any;
  valueProgress: any
  showPopup = false;
  selectedItems: Array<any> = [];
  format: any;
  loaiHoaDon: string;
  extras = [];
  paramSearch: any;
  bsModalRef: BsModalRef;
  dataInfor: any;
  ioSignature: Subscription;
  checkSubmit: any;
  formatter: any;
  objStatusBtn = {};
  bsModalSignRef: BsModalRef;
  constructor(
    private authService: AuthService,
    private modalService: BsModalService,
    private modalImportService: BsModalService,
    private router: Router,
    private searchbillService: SearchbillService,
    private route: ActivatedRoute,
    public translate: TranslateService,
    private socketService: SocketService,
    private spinner: NgxSpinnerService,
    private billService: BillService,
    private vPreviewInvService: ViewInvoiceService,
    private invoiceService: InvoiceService,
  ) {
    this._language = this.authService.getLanguage();
    this._translate = this.authService.getTranslate();
    translate.use(this._language);
    this.extras = [{
      id: '1',
      name: this._translate.CONTROLL.BUTTON.extend,
      items: [
        {
          id: 'XEM',
          name: this._translate.CONTROLL.BUTTON.view_invoice,
          icon: this._translate.ICONS.view
        },
        // {
        //   id: 'GUI_BEN_MUA',
        //   name: this._translate.CONTROLL.LABEL.send_invoice,
        //   icon: this._translate.ICONS.send_email
        // },
        // {
        //   id: 'TAI_HD_PDF',
        //   name: this._translate.CONTROLL.BUTTON.tai_hd_pdf,
        //   icon: this._translate.ICONS.download
        // },
      ]
    }];
  }
  viewDetailInvoice(data) {
    this.view(data);
  }
  ngOnInit() {
    this.configs = this.authService.getConfigs();
    this.trangThaiGuiCQTStatus = VSelectBoxService._trangThaiGuiCQT(this._translate);
    if (this.configs.system_parameter.select_invoice_current_page)
      this.pageSelect = "page";
    else
      this.pageSelect = "allPages";
    this.formatter = VChanggeMoneyService.getFormatterColumnInv(this.configs.decimal_places_json);
  }
  viewAttachFile() { }
  selectionChanged(e) {
    this.selectedItems = e.selectedRowsData;
  }
  onRowPrepared(e) {
    if (e.rowType == 'data') {
      if (e.data.adjustment_type === '2' || e.data.adjustment_type === '7' || e.data.adjustment_type === '6') {
        let eles = e.rowElement.getElementsByTagName('td');
        for (let i = 0; i < eles.length; i++) {
          if (i !== 9 && i !== 1) {
            eles[i].style.textDecoration = 'line-through';
          }
        }
      }
    }
  }
  onImportShowing(e) {

  }
  onImportHiding(e) {

  }

  displayFunction = (item) => {
    if (!item) {
      return 'Tất cả';
    }
    return item.name;
  }

  myFunction(id) {
    if (this.clickCurent !== '' && this.clickCurent !== id) {
      let valueCurrent = document.getElementById(this.clickCurent);
      if (!valueCurrent)
        document.getElementById(id).classList.toggle('show');
      else {
        document.getElementById(this.clickCurent).classList.remove('show');
        document.getElementById(id).classList.toggle('show');
      }
    }
    else
      document.getElementById(id).classList.toggle('show');
    this.clickCurent = id;
  }
  customizeText = (url) => {
    this.invoiceService.handleTotalGrid(url.value, this.objStatusBtn, this.dataGrid);
    return '';
  }

  loadList(params?) {
    this.dataSource = {};
    this.dataSource.store = this.searchbillService.getAll(params);
  }
  getSearch(e) {
    this.paramSearch = e;
    this.loadList(e);
  }
  itemExtrasClick(e) {
    if (e.itemData.id === 'XEM') {
      this.view(null);
    }
    else if (e.itemData.id == "TAI_HD_PDF") {
      this.taiHDPdf();
    }
  }
  taiHDPdf() {
  }
  view(data) {
    if (this.selectedItems.length > 0 || data) {
      const _lst = this.selectedItems;
      let is_default_tempplate = true;
      if (_lst.length > 0 || data) {
        if (this.selectedItems.length === 1) {
          this.dataInfor = this.selectedItems[0];
        } else {
          this.dataInfor = data;
        }
        let ids = [];
        if (data) {
          ids.push(data.id);
          if (!VStringService._attemptConvertBool(data.is_default_template))
            is_default_tempplate = false;
        } else {
          ids = _lst.map(e => e.id);
          let arrDefault = _lst.filter(p => VStringService._attemptConvertBool(p.is_default_template));
          let arrNotDefault = _lst.filter(p => !VStringService._attemptConvertBool(p.is_default_template));
          if (arrDefault.length > 0 && arrNotDefault.length > 0) {
            Library.notify("Bạn không thể xem hóa đơn từ phần mềm iHoadon và phần mềm bán hàng cùng lúc", 'error');
            return;
          }
          else if (arrNotDefault.length > 0)
            is_default_tempplate = false;
        }
        if (is_default_tempplate) {
          const initialState = {
            items: ids,
            isXuatHD: false,
            hinhthucxuat: '',
            typeAction: sEnum.typeCode.xem_hoa_don_mtt_html_xl
          };
          this.vPreviewInvService.xemMauPhanTrang(initialState);
        }
        else {
          if (this.configs.system_parameter.view_multi_invoice) {
            let data = this.billService.taodulieuXemMau(ids);
            data['configs'] = sEnum.cauhinhxemmau.hoadon;
            this.vPreviewInvService.xemMau(data);
          }
          else {
            const initialState = {
              items: ids,
              isXuatHD: false,
              hinhthucxuat: '',
              typeAction: sEnum.typeCode.xem_hoa_don_mtt,
              cssClass: 'modal-xl view-bill'
            };
            this.vPreviewInvService.xemMauPhanTrang(initialState);
          }
        }
      } else {
        Library.notify(this._translate.NOTIFY.no_record_selected, 'error');
      }
    } else {
      Library.notify(this._translate.NOTIFY.no_record_selected, 'error');
    }
  }
  LydoKTiepNhan(data) {
    let lstErr = JSON.parse(data.error_content);
    const initialState = {
      lstErr: lstErr,
      notifyType: "THONG_BAO_LOI_CQT"
    };
    this.bsModalSignRef = this.modalService.show(SendDataForTaxComponent, {
      backdrop: 'static',
      keyboard: true,
      class: 'modal-lg modal-margin-width',
      initialState
    });
    const subModal = this.modalService.onHide.subscribe(() => {
      subModal.unsubscribe();
    });
  }
}


